<template>
	<el-row class="container">
		<el-col :span="24" class="header">
			<el-col :span="10" class="logo" :class="collapsed?'logo-collapse-width':'logo-width'">
				{{collapsed?'':sysName}}
			</el-col>
			<el-col :span="10">
				<div class="tools" @click.prevent="collapse">
					<i class="fa fa-align-justify"></i>
				</div>
			</el-col>
			<el-col :span="4" class="userinfo">
				<el-dropdown trigger="hover">
					<span class="el-dropdown-link userinfo-inner">{{sysUserName}} <i class="el-icon-caret-bottom"></i></span>
					<el-dropdown-menu slot="dropdown">
						<el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</el-col>
		</el-col>
		<el-col :span="24" class="main">
			<aside >
			    <el-menu router :default-active="$route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="collapsed">
			    	<el-menu-item v-for="(item, index) in system_list" :index="$route.path">
			    		<a :href="item.url" class="non-trace" target="_blank">{{item.name}}</a>
			    	</el-menu-item>
					<el-menu-item :index="$route.path"><a href="http://www.baidu.com" class="non-trace" target="_blank">AMC</a></el-menu-item>
			     	<template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
			     		<template v-if="item.leaf == false">
			     			<el-submenu index="index+''">
			     				<template slot="title">
			     					<i class="el-icon-menu"></i>
			     					{{item.name}}
			     				</template>
			     				<el-menu-item v-for="(child,cIndex) in item.children" v-if="!child.hidden" :index="child.path">
			     					{{child.name}}
			     				</el-menu-item>
			     			</el-submenu>
			     		</template>
			     		<template v-else >
			     			<template v-for="(child,cIndex) in item.children" >
			     				<el-menu-item :index="child.path" v-if="!child.hidden">
				     				<i class="el-icon-menu"></i>
				     				{{child.name}}
				     			</el-menu-item>
			     			</template>
			     		</template>
			     	</template>
			    </el-menu>
			</aside>
			<section class="content-container">
				<div class="grid-content bg-purple-light">
					<el-col :span="24" class="breadcrumb-container">
							<el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
								{{ item.name }}
							</el-breadcrumb-item>
						</el-breadcrumb>
					</el-col>
					<el-col :span="24" class="content-wrapper">
						<transition name="fade" mode="out-in">
							<router-view></router-view>
						</transition>
					</el-col>
				</div>
			</section>

		</el-col>
	</el-row>
</template>

<script>
	export default {
		data() {
			return {
				sysName:'AMC',
				collapsed:false,
				sysUserName: '',
				system_list:[]
			}
		},
		methods: {
			onSubmit() {
				console.log('submit!');
			},
			handleOpen(key, keyPath) {
		        console.log(key, keyPath);
		    },
		    handleClose(key, keyPath) {
		        console.log(key, keyPath);
		    },
			handleselect: function (a, b) {
			},
			//退出登录
			logout: function () {
				var _this = this;
				this.$confirm('确认退出吗?', '提示', {
					//type: 'warning'
				}).then(() => {
					sessionStorage.removeItem('userInfo');
					_this.$router.push('/login');
				}).catch(() => {

				});
			},
			//折叠导航栏
			collapse:function(){
				this.collapsed=!this.collapsed;
			},
			showMenu(i,status){
				this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-'+i)[0].style.display=status?'block':'none';
			}
		},
		mounted() {
			var user = sessionStorage.getItem('userInfo');
			if (user) {
				user = JSON.parse(user);
				this.sysUserName = user.username || '';
			}
			//所拥有系统
			var system_list = sessionStorage.getItem('systemList');
			if(system_list){
				this.system_list = JSON.parse(system_list);
			}
		}
	}

</script>

<style type="text/css">
	.non-trace{
		text-decoration:none;
		color:#48576a;
	}
	.non-trace:hove{
		color:#48576a;
		text-decoration:none;
	}
</style>

<style scoped lang="scss">
	@import '~scss_vars';
	.container {
		position: absolute;
		top: 0px;
		bottom: 0px;
		width: 100%;
		.header {
			height: 60px;
			line-height: 60px;
			background: $color-primary;
			color:#fff;
			.userinfo {
				text-align: right;
				padding-right: 35px;
				float: right;
				.userinfo-inner {
					cursor: pointer;
					color:#fff;
					img {
						width: 40px;
						height: 40px;
						border-radius: 20px;
						margin: 10px 0px 10px 10px;
						float: right;
					}
				}
			}
			.logo {
				//width:230px;
				height:60px;
				font-size: 22px;
				padding-left:20px;
				padding-right:20px;
				border-color: rgba(238,241,146,0.3);
				border-right-width: 1px;
				border-right-style: solid;
				img {
					width: 40px;
					float: left;
					margin: 10px 10px 10px 18px;
				}
				.txt {
					color:#fff;
				}
			}
			.logo-width{
				width:230px;
			}
			.logo-collapse-width{
				width:60px
			}
			.tools{
				padding: 0px 23px;
				width:14px;
				height: 60px;
				line-height: 60px;
				cursor: pointer;
			}
		}
		.main {
			display: flex;
			position: absolute;
			top: 60px;
			bottom: 0px;
			overflow: hidden;
			aside {
				flex:0 0 230px;
				width: 230px;
				.el-menu{
					height: 100%;
				}
				.collapsed{
					width:60px;
					.item{
						position: relative;
					}
					.submenu{
						position:absolute;
						top:0px;
						left:60px;
						z-index:99999;
						height:auto;
						display:none;
					}

				}
			}
			.menu-collapsed{
				flex:0 0 60px;
				width: 60px;
			}
			.menu-expanded{
				flex:0 0 230px;
				width: 230px;
			}
			.content-container {
				// background: #f1f2f7;
				flex:1;
				// position: absolute;
				// right: 0px;
				// top: 0px;
				// bottom: 0px;
				// left: 230px;
				overflow-y: scroll;
				padding: 20px;
				.breadcrumb-container {
					//margin-bottom: 15px;
					.title {
						width: 200px;
						float: left;
						color: #475669;
					}
					.breadcrumb-inner {
						float: right;
					}
				}
				.content-wrapper {
					background-color: #fff;
					box-sizing: border-box;
				}
			}
		}

		//自定义按钮颜色
		//确定
		.btn-determine{
			background-color: $color-ok-btn;
		}
		//取消
		.btn-cancel{
			background-color: $color-cancel-btn;
		}
	}
</style>


